  static getNextPosts(author: String, startPermlink:any, list: Array<any>, size: number, tagList: Map<String, Array<any>>, keys: Array<String>) {

      // console.log(startPermlink + " -- " +date );
      steem.api.getDiscussionsByAuthorBeforeDateAsync(author, startPermlink, new Date().toISOString().split('.')[0], size, function (err, result) {
        // console.log(result);
        var post = null;
        var lastPermlink: any;
        for(var indexI = 0 ; indexI < result.length; ++indexI) {
            post = result[indexI];
            lastPermlink = post.permlink;

            if(indexI + 1 < result.length) {
              // console.log(post.author + " --- " + post.root_title);
              list.push(post);
            }
  
        }
        
        if(result.length >= size) {
          // console.log(lastPermlink);
          SearchComponent.getNextPosts(author, lastPermlink, list, size, tagList, keys);
        } else {
          for(var indexI = 0 ; indexI < list.length; ++indexI) {
            // console.log(list[indexI]);
            // console.log(list[indexI].author + " --- " + list[indexI].root_title);
          }
          SearchComponent.sortTag(list, tagList, keys);
        }
      });
      
  }

  static sortTag(list: Array<any>, tagList: Map<String, Array<any>>, keys: Array<String>) {

    var post: any = null;

    tagList['all_all'] = new Array();
    keys.push('all_all');


    for(var indexI = 0 ; indexI < list.length; ++indexI) {
      post = list[indexI];
      
      tagList['all_all'].push(post);

      var tags = JSON.parse(post.json_metadata).tags;

      for(var indexJ = 0; indexJ < tags.length; ++indexJ) {
        
        if(tagList[tags[indexJ]] === undefined) {
          tagList[tags[indexJ]] = new Array();
          keys.push(tags[indexJ]);
        }
        tagList[tags[indexJ]].push(post);

      }

    }

  }

